/* ========================
 table of contents
===========================
1. Left Section ==> Buttons
2. Right Section ==> Card
3- Responsive Design (media query)*/

main.flex {
    gap: 2.64rem;
    align-items: start;
   
  }
  
  /* ===============================
     1. Left Section ==> Buttons
  ================================== */
  main section.left-section.flex {
    flex-direction: column;
    gap: 10px;
  }
  
  main section.left-section.flex button {
    background-color: rgba(36, 37, 46, 1);
    width: 11rem;
    padding: 0.75rem 0;
    text-align: center;
    font-size: 1.05rem;
    text-transform: capitalize;
    opacity: 0.5;
    border-radius: 5px;
    transition: 0.3s;
  }
  .light   main section.left-section.flex button.active {
border: 1px solid black;
  }
  
  main section.left-section.flex button:hover {
    opacity: 1;
  }
  
  main section.left-section.flex button.active {
    opacity: 1;
    font-weight: bold;
    letter-spacing: 0.6px;
    padding: 0.8rem 0;
    border: 1px solid var(--blue);
  }
  
  /* ===============================
     2. Right Section ==> Card
  ================================== */
  
  main section.right-section.flex {
    flex-grow: 1;
    justify-content: center;
    flex-wrap: wrap;
  
    column-gap: 16px;
    row-gap: 2rem;
    
 
  }
  
  .card {
    border: 1px solid rgba(93, 188, 252, 0.3);
    border-radius: 5px;
    transition: 0.3s;
    box-shadow: 1px 1px 1px rgba(0, 0, 0, 0.2), -1px 1px 1px rgba(0, 0, 0, 0.2);

    background-image: linear-gradient(
      rgba(255, 255, 255, 0.05),
      rgba(255, 255, 255, 0.05)
    );
   
  }
.light .card ,.light .card:hover{
  border: none;
}
.light .card:hover{
  background-color: rgb(240, 240, 240);
}
  
  .card:hover {
    border: 1px solid var(--blue);
    rotate: 1deg;
    scale: 1.01;
    cursor: pointer;
  }
  
  .card img {
    border-radius: 5px;
  }
  
  .card .box {
    padding: 1rem 0.4rem;
  }
  
  .card .box .title {
    color: var(--title);
  }
  
  .card .box .sub-title {
    color: var(--subtitle);
    font-size: 0.8rem;
    margin-top: 0.7rem;
    margin-bottom: 1.1rem;
  }
  
  .card .box .icons {
    justify-content: space-between;
  }
  
  .card .icon-github,
  .card .icon-link {
    font-size: 1.2rem;
    color: var(--subtitle);
  }
  
  .card .icon-github:hover,
  .card .icon-link:hover {
    /* font-size: 1.25rem; */
    color: var(--icon-hover);
    cursor: pointer;
  }
  
  .card a.link {
    font-size: 0.9rem;
    color: var(--blue);
    margin-right: 12px;
  }
  
  
  
  /* =====================================
    3- Responsive Design (media query)
  ======================================== */
  
  @media (max-width: 600px) {
    main.flex {
      flex-direction: column;
      gap: 2.64rem;
      align-items: center;
    }
  
    main section.left-section.flex {
      flex-direction: row;
      flex-wrap: wrap;
      justify-content: center;
      gap: 10px;
    }
  
    main section.left-section.flex button {
      width: 7rem;
      padding: 0.8rem 0;
      font-size: 0.9rem;
    }
  }
  
  .page-item{
    padding: 7px 10px;
    border: 1px solid var(--border);
    color:var(--title) ;
    margin: 0 3px;
    transition: 0.3s;
  }
  .page-item:hover,
  .page-item.active{
    background-color: var(--blue);
  cursor: pointer;
  }
  .pagination-container{
    
    display: flex;
    justify-content: center;
   
    margin-top: 20px;
   
   
  }
 